const signal = (data: any): string => {
    if ((data.infectionRate > 1) || (data.caseDensity > 10) || (data.testPositivityRatio > 0.05) || (data.icuCapacityRatio >= 0.85)) {
        return 'red';
    }
    return 'green';
}


const signals: Map<string, string> = new Map<string, string>([
    ["multnomah", signal(multnomahData.metrics)],
    ["washington", signal(washingtonData.metrics)],
    ["honolulu", signal(honoluluData.metrics)],
    ["clark", signal(clarkData.metrics)],
    ["larimer", signal(larimerData.metrics)],
    ["denver", signal(denverData.metrics)],
]);

